.container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 20vh;
    background-color: #f2f2f5;
}

.footerBlock {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 70%;
    height: 70%;
}

.iconsBlock {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    min-width: 30%;
}

.icon {
    height: 50px;
    width: 50px;
    margin: 15px;
}